
	import Vue from "vue"
	import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

	export default {
		components :{
			Grid,
	    	GridItem,
	    	GroupTitle
		},
		data () {
		    return {
		      arr:"",
		    
		    }
		  },
		created(){
     
	      	  var url = "http://m.buka.cn/category/ajax_group"
  		      Vue.axios.get(url).then((res)=> {
  		        return res.data.datas.items  
  		      }).then((data)=> {
  		        this.arr = data
  		       //console.log(this.arr)
  	      })
   		 }
	}



