
import Vue from "vue"
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default {
  name: 'hello',
  components: {
	    Grid,
	    GridItem
  },
  data () {
    return {
      arr4:""
    }
  },
  created(){
       //var url = "http://m.buka.cn/ranking/get_data"
       var url = "static/list4.json"
	      Vue.axios.get(url).then((res)=> {
	        return res.data.datas.items  
	      }).then((data)=> {
	        this.arr4 = data
	        //console.log(this.arr4)
      })
    }
}



